@import url("https://fonts.googleapis.com/css2?family=Dosis&family=Noto+Sans+JP:wght@500;900&family=Noto+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Ubuntu&display=swap");

.container {
  display: flex;
  flex-direction: column;
  align-content: center;
  width: 300px;
  height: 550px;
  background: #c2f4ff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 0px 0px 100px 0px;
}

.selectBox {
  width: 184px;
  height: 44px;
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 30px;
  background-color: #44b7ff;
  border-radius: 8px;
  border: none;
  color: #ffffff;
  text-align: center;
  font-family: "Poppins";
  font-weight: 400;
  font-size: 100%;
}

.btn {
  width: 184px;
  height: 44px;
  margin-top: 30px;
  background: #44b7ff;
  /* box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); */
  border-radius: 14px;
  border: none;
  font-family: "Ubuntu";
  font-weight: 400;
  font-size: 100%;
  margin-left: 50px;
  color: #ffffff;
}

.btn:hover {
  background-color: #5dc1ff;
  transition: 0.2s;
}

.btn:active {
  transform: scale(0.97);
  box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
}

.search {
  background-color: #44b7ff;
  border: none;
  border-radius: 10px;
  padding: 10px 10px;
  /* margin-left: 0%; */
  font-size: 110%;
  color: rgb(255, 255, 255);
  outline: none;
  margin-top: 30px;
  margin-left: 10px;
}

.search::placeholder {
  color: white;
  font-family: "Poppins";
  font-weight: 400;
}

.searchButton {
  background-color: transparent;
  scale: 150%;
  color: black;
  border: none;
  border-radius: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-left: -12%;
  position: relative;
  cursor: pointer;
  margin-top: 30px;
}

.searchButton:hover {
  color: white;
}

.restoreButton {
  background-color: #44b7ff;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 10px;
  font-family: "Poppins";
  font-weight: 400;
  font-size: 100%;
  text-align: middle;
  text-decoration: none;
  width: 184px;
  white-space: nowrap;
  margin-left: -5%;
  margin-top: 30px;
}

.restoreButton:hover {
  background-color: #5dc1ff;
  color: white;
  transition: 0.2s;
}

.restoreButton:active {
  transform: scale(0.97);
}

.searchContainer {
  display: flex;
  flex-direction: column;
  /* background-color: red; */
  justify-content: center;
  align-items: center;
}

.newsearch {
  display: flex;
  flex-direction: row;
}

